import styled from "styled-components";
import Button from "./styled/Button.styled";
import fbpage from "../assets/img/fb-page.png"

const FbpageEl = styled.section`
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  text-align:center;
  row-gap: 1rem;
  padding: 0 var(--padding-container);
  margin: var(--margin-top-bottom) 0;
`;
const Title = styled.h3`

`;
const Subtitle = styled.span`

`;
const FbLink = styled.a`
  display: inherit;
  flex-direction: inherit;
  row-gap: inherit;
   > Button{
    margin: auto;
   }
`;
const FbPageImg = styled.img`
  width: 100%;
  border-radius: 12px;
`;


const Fbpage = () => {
  return (
    <FbpageEl>
      <Title>Join our Facebook Page Community</Title>
      <Subtitle>You can ask questions about admission, shifting, and etc. Get to know CvSUeos.</Subtitle>
      <FbLink href="https://www.facebook.com/groups/1238051143037808/" target="_blank" rel="noreferrer">
        <Button bold>
          Join
        </Button>
        <FbPageImg src={fbpage} alt=""/>
      </FbLink>
    </FbpageEl>
  )
}

export default Fbpage