import styled from "styled-components";

const FooterEl = styled.section`
  border-top: solid var(--primary-color) 2px;
  text-align: center;
  padding: 1rem 0;
  user-select: none;
`;

const Footer = () => {
  return <FooterEl>
      Created by <b><a href="https://www.bricesuazo.com/" target="_blank" rel="noreferrer" >Brice Suazo</a></b>
  </FooterEl>;
};

export default Footer;
