import styled from "styled-components";
import Card from "./Card";
import { Devices } from "./Theme";
import myportallogo from '../assets/img/cvsu-myportal-logo.png'
import elearninglogo from '../assets/img/cvsu-elearning-logo.png'
import librarylogo from '../assets/img/cvsu-library-logo.jpg'

const cards = [
  { icon: myportallogo, text: "Access your Grades, Schedule, and Account balance through CvSU Student Portal.", link: "https://myportal.cvsu.edu.ph/"},
  { icon: elearninglogo, text: "CvSU Open Distance E-learning is web-based learning management system using Moodle platform. The E-learning system is intended for both teachers and students of Cavite State University - Main Campus.", link: "https://elearning.cvsu.edu.ph/"},
  { title: "CvSU Registrar", subtitle:"Enrollment Information", text: "Know your subjects, available slots, and enrolled students.", link: "https://registrar.cvsu.edu.ph/"},
  { icon: librarylogo, text: "#BringingTheLibraryToYou", link: "http://library.cvsu.edu.ph/"},
];


const LinksEl = styled.article`
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  margin: var(--margin-top-bottom) 0;
`;

const LinksTitle = styled.h1`
`;
const CardContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 8px;

  @media ${Devices.Laptop}{
    flex-direction: row;
    padding: 0 var(--padding-container);
  }
`;

const CardLink = () => {
  return (
    <LinksEl>
    <LinksTitle>Links</LinksTitle>
    <CardContainer>
      {cards.map((card, i) =>
        <Card key={i} icon={card.icon} text={card.text} link={card.link} title={card.title} subtitle={card.subtitle}/>
        )}
    </CardContainer>
    </LinksEl>
  )
}

export default CardLink