import styled from "styled-components";
import { Colors, Devices } from "./Theme";
import Button from './styled/Button.styled';

const CardEl = styled.a`
    border: 2px solid ${Colors.Primary};
    border-radius: var(--border-radius);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    row-gap: 1.5rem;
    align-items: center;
    text-align: center;
    flex: 1;
    
    /* min-width: 170px; */
    @media ${Devices.Laptop}{
        margin: auto;

    }
`;
const IconImg = styled.img`
    max-width: 100%;
    @media ${Devices.Laptop}{
        width: 80%;

    }
    `;
const TitlesContainer = styled.div`
    max-width: 100%;
    white-space: nowrap;

`;
const Title = styled.h1`
    
`;
const Subtitle = styled.span`

`;
const Card = ({icon, text, link, title, subtitle}) => {
  return (
    <CardEl href={link} target="_blank" rel="noreferrer">
        <IconImg src={icon} alt="" />
        {(title, subtitle) && 
        <TitlesContainer>
            <Title>{title}</Title>
            <Subtitle>{subtitle}</Subtitle>
        </TitlesContainer>
        }
        
        {text}
        <Button>Visit</Button>
    </CardEl>
  )
}

export default Card