import styled from "styled-components";
import ic from "../assets/img/under-construction.png";

const UnderConstructionEl = styled.section`
    display: grid;
    place-items: center;
    height: calc(100vh - 120px);
    > div{
        display: inherit;
        place-items: inherit;

        > img{
            user-select: none;
        }
    }
`;

const UnderConstruction = () => {
  return (
    <UnderConstructionEl>
        <div>
            <img src={ic} alt="" height="150" draggable="false"/>
            <span>
                This page is under construction. 
            </span>
        </div>
    </UnderConstructionEl>
  )
}

export default UnderConstruction