import styled from "styled-components";
import { Devices } from "./Theme";

const DividerEl = styled.section`
    background-color: var(--primary-color);
    text-align: center;
    justify-content: center;
    text-justify: center;
    color: var(--white);
    padding: 1rem 0;
    letter-spacing: 4px;
    white-space: nowrap;

    @media ${Devices.Laptop}{
      letter-spacing: 16px;
    }
`;

const Divider = () => {
  return (
    <DividerEl>
        TRUTH    EXCELLENCE    SERVICE
    </DividerEl>
  )
}

export default Divider