import styled from "styled-components";
import CvSUBGImg from '../assets/img/AerialView.jpg';
import CvSULogoImg from '../assets/img/cvsu-logo.png';
import CvSULayaAtDiwaImg from '../assets/img/laya-at-diwa.png';
import { Devices } from "./Theme";


const IntroEl = styled.section`
    overflow: hidden;
    height: calc(100vh - 4rem);
    `;
const Container = styled.div`
    height: 100%;
    position: relative;
    background-image: url("${CvSUBGImg}");
    background-repeat: no-repeat;
    background-size: cover;
    /* transform: scale(1.05); */
    background-color: rgba(0,0,0,.5);
    /* filter: blur(10px); */

    ::before{
        content: "";
        background-color: rgba(0, 0, 0, 0.5);
        height: 100%;
        width: 100%;
        display: flex;
    }

    `;
const LayaAtDiwa = styled.img`
    position: absolute;
    z-index: 2;
    bottom: -10px;
    height: 90%;
    user-select: none;
    
    @media ${Devices.MobileS}{
        right: -20px;
        height: 70%;
    }
    @media ${Devices.MobileM}{
        height: 60%;

    }
    @media ${Devices.Tablet}{
        right: 8px;
        height: 75%;
    }
    @media ${Devices.Laptop}{
        height: 80%;
        right: var(--padding-container);

    }
    `;
const TitleContainer = styled.div`
    text-align: center;
    position: absolute;
    top: 4rem;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto; 
    z-index: 2;
    > *{
        color: var(--white);
    }
`;
const CvSULogo = styled.img`
    height: 128px;
    user-select: none;
    `;
const Title = styled.h1`
    font-size: 2.5rem;
    @media ${Devices.Tablet}{
        font-size: 4rem;
    }
    @media ${Devices.Laptop}{
        font-size: 5rem;
    }
`;
const Subtitle = styled.h2`
    font-size: 1.25rem;
    @media ${Devices.Laptop}{
        font-size: 2rem;
        
    }
`;

const VisMisContainer = styled.div`
    width: 280px;
    text-align: center;
    > *{
        color: var(--white);
    }
`;
const VisMisTitle = styled.h3`

`;
const VisMis = styled.p`
    
`;


const Intro = () => {
  return (
    <IntroEl>
        <Container>
            <LayaAtDiwa src={CvSULayaAtDiwaImg} alt="CvSU Laya at Diwa"/>
            <TitleContainer>
                <CvSULogo src={CvSULogoImg} alt="" />
                <Title>
                    Cavite State University
                </Title>
                <Subtitle>Truth  |  Excellence  | Service</Subtitle>
            </TitleContainer>

            <VisMisContainer>
                <VisMisTitle>
                    Mission
                </VisMisTitle>
                <VisMis>
                Cavite State University shall provide equitable and relevant education opportunities in the arts, sciences and technology through quality instruction and responsive research and development activities. It shall produce professional, skilled and morally upright individuals for global competitiveness.
                </VisMis>
            </VisMisContainer>
        </Container>
    </IntroEl>
  )
}

export default Intro