import styled from "styled-components";
import LogoImg from "../assets/img/cvsu-logo.png";
import {Link} from 'react-router-dom';
import Button from "./styled/Button.styled";
import { Colors, Devices } from "./Theme";
import {AiFillCaretDown} from 'react-icons/ai'
import {BiLinkExternal} from 'react-icons/bi'

const HeaderEl = styled.section`
    display: flex;
    justify-content: space-between;
    background-color: var(--primary-color);
    user-select: none;
    align-items: center;
    padding: .5rem 12px;
    white-space: nowrap;
    @media ${Devices.Laptop}{
        padding: .5rem var(--padding-container);
    }
    `;

const LeftContainer = styled(Link)`
    display: flex;
    flex-direction: row;
    align-items: center;
    column-gap: 8px;
    `;
const HeaderLogo = styled.img`
    width: 4rem;
    `;
const TitleContainer = styled.div`
    > * {
        color: var(--white);
    }
    `;
const Title = styled.h3`
    font-size: 1rem;

    @media ${Devices.Laptop}{
        font-size: 1.25rem;
    }

    `;
const Subtitle = styled.span`
    font-size: .9rem;
`;
const RightContainer = styled.div`
    display: inherit;
    column-gap: 4px;
`;
const HeaderLinkEl = styled(Link)`
    display: none;
    
    :hover{
        background-color: var(--secondary-color);
    }
    
    @media ${Devices.Laptop}{
        display: flex;
        align-items: center;
        padding: 0 .5rem;
        border-radius: 8px;
        color: var(--white);
        
    }
`;
const DownButton = styled.span`
    color: ${Colors.White};
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0 .7rem;
    border-radius: 8px;
    * {
        color: ${Colors.White};
    }
    @media ${Devices.Laptop}{
        display: none;
    }
    :hover{
        background-color: var(--secondary-color);
    }
`;
const HeaderLink = ({text, link}) => {
    return(
        <HeaderLinkEl to={link}>
            {text}
        </HeaderLinkEl>
    )
}

const Header = () => {
  return (
    <HeaderEl>
        <LeftContainer to="/">
            <HeaderLogo src={LogoImg} alt="" />
            <TitleContainer>
                <Title>Cavite State University</Title>
                <Subtitle>Information Center</Subtitle>
            </TitleContainer>
        </LeftContainer>

        <RightContainer>
            <HeaderLink text="Home" link="/"/>
            <HeaderLink text="Course Offered" link="/course-offered"/>
            <HeaderLink text="Colleges" link="/colleges"/>
            <HeaderLink text="Campuses" link="/campus"/>
            <DownButton><AiFillCaretDown /></DownButton>
            <a href="https://cvsu.edu.ph" target="_blank" rel="noreferrer"> <Button>Official Website <BiLinkExternal /></Button></a>
        </RightContainer>
    </HeaderEl>
  )
}

export default Header